import React, { useEffect, useState } from 'react'
import logoImg from '../assets/logo.png'
import { Menu, Dropdown, message } from 'antd';
import { CaretDownOutlined } from '@ant-design/icons';
import defaultAvatar from '../assets/defaultAvatar.jpg'
import { useNavigate } from 'react-router-dom'

function Header() {
    const navigate = useNavigate()
    const [avatar, setAvatar] = useState(defaultAvatar)
    const [username, setUsername] = useState("游客")

    // 模拟componentDidMount
    useEffect(() => {
        let username1 = localStorage.getItem('username')
        let avatar1 = localStorage.getItem('avatar')
        if (username1) {
            setUsername(username1)
        }
        if (avatar1) {
            setAvatar('http://47.93.114.103:6688/' + avatar1)
        }
        function checkavatar (e){
            const item = e.avatar
            if(item)
            {
                setAvatar('http://47.93.114.103:6688/' + item)
            }
        }

        const originalSetItem = localStorage.setItem;
		localStorage.setItem = function (key, newValue) {
			const setItemEvent = new Event('setItemEvent');
			setItemEvent[key] = newValue;
			window.dispatchEvent(setItemEvent);
			originalSetItem.apply(this, [key, newValue]);
		};

        window.addEventListener('setItemEvent',checkavatar)
        return ()=>{
            window.removeEventListener('setItemEvent',checkavatar)
        }
    }, [])

    // 退出登录
    const logout = () => {
        message.success('退出成功，即将返回登录页')
        localStorage.clear();   // 清除localStorage中的数据
        setTimeout(() => navigate('/login'), 1500)
    }

    const changeUsrData = () => {
        message.success('跳转至修改信息')
        setTimeout(() => navigate('/means'), 500)
    }

    const menu = (
        <Menu
            items={[
                { label: "修改信息", key: 1 , onClick:changeUsrData},
                { type: 'divider' },
                { label: "退出登录", key: 2 , onClick:logout },
            ]}
        >
        </Menu>
    );

    return (
        <header>
            <img src={logoImg} alt="" className="logo" />
            <div className="right">
                <Dropdown overlay={menu}>
                    <a className="ant-dropdown-link" onClick={e => e.preventDefault()} href="/cms-manage/!" >
                        <img src={avatar} className="avatar" alt="" />
                        <span>{username}</span>
                        <CaretDownOutlined />
                    </a>
                </Dropdown>
            </div>
        </header>
    )
}
export default Header